export class GreenForestEnemy{
    gameinfo;
    datainfo;
    constructor(gameinfo,datainfo) {
        this.gameinfo = gameinfo;
        this.datainfo = datainfo;
    }
    name = "";
    baseHp = 0;
    baseDmg = 0;
    rewardXp = 0;
    rewardCoin = 0;
    shield = false;
    imgsrc = "";
    makeEnemy(){
        let chance = Math.random()*100;
        //console.log(this.datainfo.xp)
        if(chance>90){
            return new KeeperOfForest(this.gameinfo,this.datainfo);
        }else if(chance>65){
            return new LeafFairy(this.gameinfo,this.datainfo);
        }else{
            return new Slime(this.gameinfo,this.datainfo);
        }
    }
}
class Slime extends GreenForestEnemy{
    ginfo;
    dinfo;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
    }
    name = "Slime";
    baseHp = 200 + (25*Math.floor(this.datainfo.xp/500));
    baseDmg = 20 + (2*Math.floor(this.datainfo.xp/500));
    rewardXp = Math.floor(this.baseHp/10);
    rewardCoin = Math.floor(this.baseHp/10);
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1000837088050155560/slime.png";
}
class LeafFairy extends GreenForestEnemy{
    ginfo;
    dinfo;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
    }
    name = "Leaf Fairy";
    baseHp = 320 + (40*Math.floor(this.datainfo.xp/500));
    baseDmg = 30 + (3*Math.floor(this.datainfo.xp/500));
    rewardXp = Math.floor(this.baseHp/5);
    rewardCoin = Math.floor(this.baseHp/4);
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1003276954889371729/Leaf_fairy.png";
}
class KeeperOfForest extends GreenForestEnemy{
    ginfo;
    dinfo;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
    }
    name = "Keeper of Forest";
    baseHp = 600 + (75*Math.floor(this.datainfo.xp/500));
    baseDmg = 50 + (5*Math.floor(this.datainfo.xp/500));
    rewardXp = Math.floor(this.baseHp/2);
    rewardCoin = Math.floor(this.baseHp/1.5);
    shield = true;
    baseShield = 100 + (10*Math.floor(this.datainfo.xp/500));
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1003277272008110120/keeper_of_forest.jpg";
}