export class Weapon{
    constructor() {

    }
    name = "";
    damage = 0;
    star = 0;
    code = "";
    info = "";
    imgsrc = "";
    findWeapon(code,level){
        switch (code){
            case "w3001":
                return new AtilaSword(level);
            case "w3002":
                return new EagleSword(level);
            case "w3003":
                return new LionRoar(level);
            case "w3004":
                return new StoneBreaker(level);
            case "w3005":
                return new PowerSword(level);
            default:
                break;
        }
    }
}
class AtilaSword extends Weapon{
    level = 0;
    damage = 20 + 5*(this.level - 1);
    constructor(Level) {
        super();
        this.level = Level;
        this.damage = 20 + 5*(this.level - 1);
    }
    name = "Atila Sword";
    star = 3;
    code = "w3001";
    info = "A handy sword wielded by many adventurers.";
    imgsrc = "https://media.discordapp.net/attachments/1004035772300673044/1004048662592245901/Atila_sword.jpg?width=362&height=603"
}
