export class Chest{
    constructor() {
    }
    open(){

    }
    imgsrc = ""
    name = ""
    findChest(code){
        switch(code){
            case "ch001":
                return Weapon3star.prototype.open();
            case "ch002":
                return Character4star.prototype.open();
            default:
                return null;
        }
    }
}
export class Weapon3star extends Chest{
    constructor() {
        super()
    }
    imgsrc = "https://media.discordapp.net/attachments/1004035722145177751/1004414071791177819/weapon3star_chest.jpg";
    name = "Uncommon Weapon Chest"
    open(){
        let num = Math.floor(1+Math.random()*5);
        let code = "";
        if(num/10 >=1){
            code = "w30" + num;
        }else{
            code = "w300" + num;
        }
        return code;
    }
}
export class Character4star extends Chest{
    constructor() {
        super()
    }
    imgsrc = "https://media.discordapp.net/attachments/1004035722145177751/1004414120017273003/character4star_chest.jpg";
    name = "Rare Character Chest"
    open(){
        let num = Math.floor(1+Math.random()*4);
        let code = "";
        if(num/10 >=1){
            code = "c40" + num;
        }else{
            code = "c400" + num;
        }
        return code;
    }
}
