class Salazar extends Character{
    level = 0;
    ginfo;
    baseHp = 240;
    constructor(Level,ginfo) {
        super();
        this.level = Math.min(Level,5);
        this.ginfo = ginfo;
        this.baseHp = 240 + 40*(this.level-1);
        //console.log(this.ginfo)
    }
    name = "Salazar";
    star = 4;
    code = "c4003";
    info = "A renowned knight from faraway lands.";
    stackOfBravery = 0;
    skill1 = true;
    skill1name = "Power of the Brave"
    skill1info(Level){
        return <p>Deals a normal attack with extra damage. Adds <span className="blue-text">{20 + 5*(Level-1)}%</span> stackable damage for each Stack of Bravery and consumes all of them.</p>
    }
    //skill1info = `Deals a normal attack with extra damage. Adds ${20 + 5*(this.level-1)}% stackable damage for each Stack of Bravery and consumes all of them.`
    skill1imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1003623584087941191/Power_of_the_brave.jpg";
    skill1avail = true;
    passive = true;
    passiveinfo(Level){
        return <p>Bravery: Each successful charged attack grants a Stack of Bravery.</p>
    }
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1003623098140069898/2cef40a24bcefe0cafa7191adddaa4d0.png";
    normalAttack(){
        //console.log(this.ginfo)
        setTimeout(()=>{
            let hitDmg = this.ginfo.heroDamage + Math.floor((Math.random()-0.5)*(this.ginfo.heroDamage/10));
            this.ginfo.setEnemySideText(<> </>);
            if(this.ginfo.enemyShield){
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
                this.ginfo.setEnemyShieldHp(prevState => {
                    return prevState - hitDmg
                })
            }else {
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="green-text">-{hitDmg} Health</span></FloatingMessage></>)
                this.ginfo.setEnemyHp(prevState => {
                    return prevState - hitDmg
                })
            }
        },0)

    }
    chargedAttack(){
        setTimeout(()=>{
            //applying buff
            const heroBuff = document.querySelector("#heroBuff");
            if(this.stackOfBravery > 0){
                let newnode = document.createElement("div");
                newnode.id = "SalazarBravery";
                newnode.innerHTML = `<div class="buffBox" style='background-image: url("https://cdn.discordapp.com/attachments/696274487657758720/1003623584087941191/Power_of_the_brave.jpg")'>
                            
                    </div>
                    <div class="buffTooltip">
${this.stackOfBravery} Stack of Bravery Collected.
                    </div>`
                newnode.firstChild.addEventListener("mouseenter",()=> {
                        document.querySelector("#SalazarBravery .buffTooltip").style.opacity = "100%";
                    }
                )
                newnode.firstChild.addEventListener("mouseout",()=>{
                    document.querySelector("#SalazarBravery .buffTooltip").style.opacity = "0%";
                })
                if(heroBuff.querySelector("#SalazarBravery")===null){
                    heroBuff.appendChild(newnode);
                }else {
                    for (let herobuff of heroBuff.children) {
                        if (herobuff.id === "SalazarBravery") {
                            heroBuff.replaceChild(newnode, herobuff);
                        }
                    }
                }
            }else{
                for(let herobuff of heroBuff.children){
                    if(herobuff.id==="SalazarBravery"){
                        heroBuff.removeChild(herobuff);
                    }
                }
            }
            let chance = Math.random();
            if(chance > 0.5){
                this.stackOfBravery = this.stackOfBravery+1;
                if(this.stackOfBravery > 0){
                    let newnode = document.createElement("div");
                    newnode.id = "SalazarBravery";
                    newnode.innerHTML = `<div class="buffBox" style='background-image: url("https://cdn.discordapp.com/attachments/696274487657758720/1003623584087941191/Power_of_the_brave.jpg")'>
                            
                    </div>
                    <div class="buffTooltip">
${this.stackOfBravery} Stack of Bravery Collected.
                    </div>`
                    newnode.firstChild.addEventListener("mouseenter",()=> {
                            document.querySelector("#SalazarBravery .buffTooltip").style.opacity = "100%";
                        }
                    )
                    newnode.firstChild.addEventListener("mouseout",()=>{
                        document.querySelector("#SalazarBravery .buffTooltip").style.opacity = "0%";
                    })
                    if(heroBuff.querySelector("#SalazarBravery")===null){
                        heroBuff.appendChild(newnode);
                    }else {
                        for (let herobuff of heroBuff.children) {
                            if (herobuff.id === "SalazarBravery") {
                                heroBuff.replaceChild(newnode, herobuff);
                            }
                        }
                    }
                }
                let hitDmg = this.ginfo.heroDamage + Math.floor((Math.random()-0.5)*(this.ginfo.heroDamage/10));
                this.ginfo.setEnemySideText(<> </>);
                if(this.ginfo.enemyShield){
                    this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{2 * hitDmg} Shield</span></FloatingMessage></>)
                    this.ginfo.setEnemyShieldHp(prevState => {
                        return prevState - 2 * (hitDmg)
                    })
                }else {
                    this.ginfo.setEnemySideText(<><FloatingMessage><span className="green-text">-{2 * hitDmg} Health</span></FloatingMessage></>)
                    this.ginfo.setEnemyHp(prevState => {
                        return prevState - 2 * (hitDmg)
                    })
                }
            }else{
                this.ginfo.setEnemySideText(<> </>);
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="red-text">Missed</span></FloatingMessage></>)
            }
        },0)
    }
    skill1Attack(){
        setTimeout(()=>{
            const heroBuff = document.querySelector("#heroBuff");
            for(let herobuff of heroBuff.children){
                if(herobuff.id==="SalazarBravery"){
                    heroBuff.removeChild(herobuff);
                }
            }
            let hitDmg = Math.floor(this.ginfo.heroDamage*((1.2+0.05*(this.level-1))**this.stackOfBravery)) + Math.floor((Math.random()-0.5)*(this.ginfo.heroDamage/10));
            this.ginfo.setEnemySideText(<> </>);
            if(this.ginfo.enemyShield){
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield<br/>({Math.floor((1.2+0.05*(this.level-1))**this.stackOfBravery*100-100)}% Bonus)</span></FloatingMessage></>)
                this.ginfo.setEnemyShieldHp(prevState => {
                    return prevState - hitDmg
                })
            }else {
                this.ginfo.setEnemySideText(<><FloatingMessage><span className="green-text">-{hitDmg} Health<br/>({Math.floor((1.2+0.05*(this.level-1))**this.stackOfBravery*100-100)}% Bonus)</span></FloatingMessage></>)
                this.ginfo.setEnemyHp(prevState => {
                    return prevState - hitDmg
                })
            }
            this.stackOfBravery = 0;
        },0)
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
    }

}
